main {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}
.content {
  width: 1280px;
  margin-right: 20px;
  margin-left: 20px;
  margin-top: 10px;
}

.bomb__video_area {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;

  .bomb__screenshots {
    width: 50%;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 20px;

    img {
      width: 100%;
    }
  }

  .bomb__video {
    width: 50%;

    img {
      width: 100%;
    }
  }
}
.add__video{
 height: 150px;
 width: 450px;
 background: #1F1F1F;
 border-radius: 10px;
 display: flex;
 flex-flow: column;
 align-items: center;
 z-index: 999;
}
.popup__info{
  border-radius: 10px;
  background: #101214;
  width: 250px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  letter-spacing: 1px;
  position: absolute;
  margin-top: 30px;
}
.video__link{
  width: 350px;
  border-radius: 5px;
  height: 35px;
  background: transparent;
  border: 2px solid #0085FF;
  margin-top: 50px;
  padding-left: 10px;
  color: #757575;
}
.add__wrap{
  width: 100%;
  height: 35px;
  display: flex;
  justify-content: center;
}
.add__video__btn{
  height: 35px;
  width: 35px;
  margin-left: 10px;
  border-radius: 5px;
  margin-top: 50px;
  background: #0085FF;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-weight: 700;
  font-size: 20px;
  cursor: pointer;
  transition: .3s;
}
input{
  outline: none;
}
.add__video__btn:hover{
 opacity: 0.7;
}
.circle__value{
  position: absolute;
  height: 17px;
  width: 17px;
  background: #fff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  margin-top: 60px;
  margin-right: 395px;
}
.buttons__wrap{
  display: flex;
  width: 100%;
  height: 30px;
  margin-top: 68px;
  margin-left: 57px;
}
.button__video{
  width: 110px;
  border-radius: 5px;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  height: 100%;
  cursor: pointer;
  color: #fff;
  font-size: 13px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: .3s;
}
.button__video:hover{
  scale: 1.05;
}
.accept__btn{
  width: 150px;
  background: #0085FF;
}
.cancell__btn{
  margin-left: 20px;
  background: #979797;
}