.board {
  background: #181a1e;
  width: 1220px;
  margin-left: 10px;
  margin-right: 10px;
  height: 600px;
  margin-top: 10px;
  display: flex; /* column for strategy */
}
.map__instruments {
  padding-top: 5px;
  height: 100%;
  min-width: 57.6px;
  background: #101214;
}
.map__wrapper {
  height: 240px;
  display: flex;
  flex-flow: column;
  grid-gap: 5px;
  align-items: center;
  background: #101214;
}

.closer {
  width: 100%;
  height: 20px;
}
.path {
  width: 14.57px;
  height: 7.85px;
}

.canvas {
  background: #101214;
  height: 400px;
}
@media(max-width: 1200px){
  .board{
    width: 862px;
  }
}
@media(max-width: 890px){
  .board{
    background: none;
    width: 470px;
    height: 600px;
  }
}